<template>
  <div class="home">
    <div class="home-content">
      <!--头部-->
      <div :class="$route.params.id == undefined ? 'header-top' : 'header-nav'">
        <div class="mdui-appbar mdui-appbar-fixed">
          <header-top :kfshow.sync="kfshow">
            <img
              width="28"
              height="28"
              src="../../assets/image/detail/10.png"
              alt="menu"
              class="icon-menu"
              slot="menu"
              @click="goBack()"
            >
            <router-link to="/home" slot="title" class="title-img">
              <img :src="logo?config.cdnbase+logo:''" alt="title" height="19">
            </router-link>
            <img
              width="28"
              height="28"
              src="../../assets/image/logo.png"
              alt="logo"
              class="logo"
              slot="home"
              @click="kfShow()"
            >
            <!--客服-->
          </header-top>
        </div>
      </div>
      <!--内容-->
      <div class="aboutContent">
        <div class="aboutBody">
          <h2>{{footerBody.title}}</h2>
          <div class="body_content">
            <div v-html="footerBody.body"></div>
          </div>
        </div>
      </div>
      <!--底部-->
      <min-footer/>
    </div>
  </div>
</template>
<style>
  .aboutContent .body_content div{
    width:100% !important
  }
</style>
<style lang="stylus" scoped>
  .mdui-btn:not(.mdui-ripple):active, .mdui-fab:not(.mdui-ripple):active, .mdui-btn:hover, .mdui-fab:hover {
    background: #fff !important;
  }

  .home {
    .header-top {
      height: 77px;
    }

    .header-nav {
      height: 40px;
    }

    .mdui-tab::-webkit-scrollbar {
      display: none;
    }

    .mdui-tab.mdui-color-theme {
      margin-left: 8px;
      min-height: 36px;
    }

    .mdui-tab.mdui-color-theme a {
      min-height: 36px;
      min-width: min-content;
      padding: 0 8px;
    }

    .mdui-btn {
      width: 0;
    }

    .mdui-appbar {
      box-shadow: none;
      background-color: #fff;
      border-bottom: 1px solid #EDEDED;
      margin: 0 auto;
      max-width: 768px;
    }

    .logo, .title-img, .icon-menu {
      cursor: pointer;
    }

    .active {
      color: #B4272D;
      border-bottom: 3px solid #B4272D;
    }

    .aboutBody {
      background: #fff;
      padding: 10px 2.1%;
      min-height: calc(100vh - 60px);

      h2 {
        font-size: 20px;
        padding: 10px 0;
        text-align: center;
        color: #636363;
      }

      div {
        padding: 0 5px 196px;
      }
    }
  }

  .title-img {
    position: absolute;
    left: 45px;
    margin: 0 5px;
    top: 5px;
  }

  .main-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background: #fafafa;
  }

</style>
<script>
import { footerBody } from "../../lib/goods";
import { mapState } from "vuex";

export default {
  data() {
    return {
      firstLists: "",
      secondList: "",
      kfshow: false,
      logo: "",
      siteInfo: {},
      footerBody: ""
    };
  },
  async created() {
    this.logo = this.$store.state.siteInfo.logo;
    this.siteInfo = this.$store.state.siteInfo;
    this.getFooter();
  },
  methods: {
    goBack() {
      this.$router.push("/home");
    },
    kfShow() {
      this.kfshow = !this.kfshow;
    },
    async getFooter() {
      let footer = await footerBody.get_footer_body(this);
      if (!footer.Error) {
        this.footerBody = footer;
      }
    }
  },
  watch: {
    $route() {
      //重新获取数据
      this.getFooter();
    }
  },
  computed: {
    ...mapState(["config"]),
  }
};
</script>
